/* src/components/MintingPage.css */

.minting-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  font-weight: bold;
}

.card {
  width: 90vw;
  height: 70vh;
  background-image: url("./CptloBackground.png");
  background-size: cover;
  padding: 2rem;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.minting-controls {
  display: flex;
  align-items: center;
  justify-content: center; /* center the content horizontally */
  margin-bottom: 1rem;
}

.minting-controls button {
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 0.5rem 1rem;
  font-size: 1rem;
  cursor: pointer;
  margin:  1rem;
}

.minting-controls span {
  font-size: 1.5rem;
  font-weight: bold;
}

button {
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 0.5rem 1rem;
  font-size: 1rem;
  cursor: pointer;
  margin-bottom: -11rem;
}

button:hover {
  background-color: #0056b3;
}

button:active {
  background-color: #004085;
}
@media (max-width: 768px) {
  .minting-container {
    padding: 1rem;
  }

  .card {
    width: 90%;
    height: 50vh;
    padding: 1rem;
  }

  .minting-controls button {
    font-size: 0.8rem;
    padding: 0.5rem;
    margin: 0.5rem;
  }

  .minting-controls span {
    font-size: 1.2rem;
  }

  button {
    font-size: 0.8rem;
    padding: 0.5rem;
    margin-bottom: -6rem;
  }
}
